赵瓦

分贝村的一天是由村里的广播开始的，伴随着村支书李丰收精心选取的歌单【妹妹看我、狼爱上羊、爱情买卖、香水有毒等】，村民们从沉睡中渐渐醒来，小卖部老板张来富已经出门准备开店，而老板娘文香还在屋内熟睡，王话梅在鱼塘边处理着鱼饲料，她的儿子吴大壮刚刚从麻将馆回到家中打算睡上一整天。吴秋月正在哄着儿子周小贝起床，一家之主的周大宝已经下地在干活了。弹棉郎赵瓦刚刚起床正在洗漱，他的女儿赵阿芝推开房门跑了出去在芦苇地里开心的笑着。

地点：分贝村 总户数：50户 姓名：赵瓦 性别：男 年龄：40岁 身份：村民

职业：弹棉郎 房屋面积：140平米（普通装修）

往事：

你从小就没见过你娘，你爹也在你12岁那年因病死亡，现在你还记得你爹临走前拉着你的手说：“瓦啊...你记住，认定要过一辈子的人就要一直对她好”。父亲死后，你被村里80岁的弹棉郎收养，跟着他学习本事。他无儿无女，将一身的手艺交给你以后也安详的离开了。

19岁时你练就了一手出神入化的弹棉花绝技，成功的养活了自己。你爹娘除了一张英俊无比的脸以外，几乎什么都没有留给你，但也正是这张脸，你成为了村里不少女人的梦中情人，在众多爱慕者中只有文香打动了你的心。这是个能动手绝不和你嚷嚷的彪悍女人。只是因为她很真实，喜欢什么讨厌什么从来不扭捏，别人看着她是泼妇，但你知道她所有的温柔都给了你。

文香的父亲文山很不喜欢你，文香是他的老来女，你知道文山一直想把她嫁个好人家，而你只是一个穷小子。他一直不准文香与你往来，但这点小问题难不住你们，文香不仅背着他爹和你见面，还经常塞钱贴补你，她从来没有嫌弃你穷，你看着她给你的那些钱对着她发誓，总有一天会让她过上好日子。在一起没多久她就怀了你的孩子，为了让孩子顺利生下来不被他爹发现，那段时间你带着文香躲到了镇上，镇上的生活水品很高你们过得很是不容易。

孩子快出生的时候，文香去街上买菜，刚好遇见小混混斗殴。其中一个人在撞倒文香后跑了，等你得到消息时文香已经被送去了医院。因为跌倒在楼梯上碰到了肚子，导致文香大出血，孩子却因为这次的意外生下来没几分钟就死了。医院通知了文山，他来了以后给了你一耳光，你们俩一起在产房门口等。孩子没有保住，文山为此非常气愤，你知道他怨恨你，是你害得文香这个样子。你也怨恨自己，如果这一切没有发生，文香不会跟着你吃了这么多苦。离开时，你在医院大厅里忍不住放声痛哭，为了孩子也为了文香。

文山让你离开分贝村再也不要回来，你知道现在的自己什么都给不了文香，只能黯然的离开，甚至没来得及和文香再见一面。离开医院后你回到村子收拾行囊，跑去了镇上谋生。你没什么文化也没有人脉，只能靠着在工地做苦力赚点生活费。后来你认识了镇上的混混，为了赚钱加入了当地的帮派，也算是风光过一阵，你没有再找别的女人心中一直想着文香。没几年镇上开始打黑，你们帮派大部分都坐牢了，而你在兄弟的帮助下成功逃脱，他却进了监狱，一判就是20年。分开时他告诉你，希望你可以照顾他的女儿阿芝，你欠了别人一条命，他这个要求你怎么可能不答应。

没有可以去的地方，你只能回到分贝村，因为担心警察上门你在家中躲了一个月才敢出来。出来后才知道文山已经去世了，当年你走后，他立马给文香找了个叫张来富的上门女婿，这男人是个窝囊废什么本事都没有，但好在不会欺负文香。这一年你已经26了，而阿芝刚满2岁，接到阿芝时你才知道这是个弱智的孩子，母亲早就跑了，你的兄弟这么拼命也只是为了赚钱给她一个好些的生活。你发誓等你兄弟出来以后，一定交给他一个完完整整的阿芝。离开他家之前，你看到他抱着阿芝的照片，想着给阿芝留个纪念于是也一起带走了。

你一个大男人哪里会带孩子，更何况还是个女娃。多亏村里的女人热心，见着阿芝可怜常常照顾她，让你省了不少事。你也常常教导阿芝，可惜一直没有什么效果，阿芝的智力一直都像是小孩。让你没想到的是文香这几年过得很不顺，她十分讨厌张来富，两人结婚一年就分房睡，只是村里离婚终究不是什么体面的事才生生的忍了下来，你们再见以后，文香没有提及当年的事，但你能够感觉到，她还对你有感情，甚至没有一点怨恨。如今文香在村里开了个小卖部，常常打发张来富去守着，那几年你和文香可以说是肆无忌惮的在她家偷情。你原本想再让文香怀孕，可是都没有成功，一次你去医院检查才知道，你精子衰弱，很难让女人怀孕，哪怕怀上了也大概率会流，产，但只要怀上后能够保胎到八个月以上那就没有问题。看来当初和你文香失去的是你这辈子唯一可能有的孩子。

如今：
          
今年你也已经40了，往文香那里跑得少了，但你们的感情确十分稳定。傻闺女赵阿芝刚满16，生了一副好样貌只可惜还是傻，给一颗冰糖就跟着走。平日里话都说不清楚，只有亲近的人才肯开口说点什么。村里的人都叫她小哑巴。分贝里的生活无非就是家长里短，鸡毛蒜皮。

就比如你的牌友吴大壮，村里出了名的游手好闲，25岁的年纪还被她妈王话梅追着打。这人打牌很是赖皮，赢了也不还钱，输了就打欠条。他水平又差又爱玩常常在麻将室一玩就是一个通宵，如果欠多账了他就找他妈，王话梅这个人泼妇的很，常常撒泼耍赖的不还钱，真是有什么妈就有什么儿子。王话梅当年女儿走失以后很是难过，所以对儿子溺爱成这样，她女儿走失时还很小，这么些年她还是不放弃寻找但能够找回来的几率实在是太小了。

说到废物就不得不提起张来富，这么多年被文香压得死死的，偶尔还会偷着去打麻将，明明是个吃软饭的居然还有钱赌。前几年不知道为什么文香会给他买车，你看见那个奥拓就生气，这个混蛋居然还敢骂你穷酸，你一气之下扎了他的四个轮胎，那叫一个爽快。

要说起村里你最看不惯谁，那就要属李丰收了。文山死了以后他当上了村支书，就因为他是村里唯一的高中生，仗着在镇里多读了几年书，说话做事都是十分的酸，一身盗版的阿尼玛西装更是常年都不换，天天说是要为了村民谋福利，但是收起管理费却是一点都不手软，这些年也不知道捞了多少的油水。之前他想要涨管理费，但这么多年都没有变过凭什么他可以涨，你带了一帮村民去把他给堵了，他还坚持要涨，你直接就把他给打了还把他办公室给砸了，之后的管理费你从来没有交过，并且还通知了十村八店的弹棉郎封杀李丰收，不准给他弹被子。

你和村里的猎户周大宝私底下关系不错，这是个老实汉子是文香的干弟弟，文山还在世时认的干儿子，只是知道的人不多。当初你进山被捕兽夹弄伤还是他救的你。后来他娶了一个镇上的女人吴秋月，吴秋月因为当初大着肚子进村，又因为长得漂亮，没少受以王话梅为首的女人们编排。文香又因为吴秋月年轻，对她也不太喜欢。为了避免让文香误会，你和吴秋月几乎没有接触。不过吴秋月这个人清高的很，平时遇见你们从来不打招呼。

最近
          
李丰收毕恭毕敬的迎来了一堆城里人，说是什么大公司看上了分贝村，准备把这里拆迁了。拆迁代表什么？代表着你们可以得到不少的钱啊！可是你却犯了愁，当年的那些事情不知道你还有没有案底，在村里躲了这么些年，要是因为这事让你进去了，那可就得不偿失了。还好村里的老人们根本不愿意离开，这城里人想要赶走你们那可是还得费不少功夫呢。

【拆迁第一天 2010年9月5日 晴转多云】

周大宝死了，得到消息时你非常惊讶，听说是进山时掉下了山谷。以他的身手怎么可能会发生这种事，因为你家和吴秋月家比较近，你赶紧冲到了吴秋月家去通知她，她正在准备午饭，得到消息后六神无主的跟着你去了村南。到的时候一堆人围在那里，吴秋月急忙挤了进去，没一会就听见里面传来她的哭声。接着你看到文香怒气冲冲的冲了进来，进来提着吴秋月就是一顿捧，你和李丰收立刻上前劝架。后来李丰收就扯着文香走了。你看吴秋月已经晕了过去，而其他人则是在一旁看着热闹，走上前将她背了起来送回了家中，看着家中还有个小孩你让其中一个跟着看热闹的村妇留下来照看后就离开了。

周大宝的尸体还摆在那里，大家还不知道要怎么做，只能在一旁等着，过了一会老村长来了，竟然叫大家直接把周大宝拖去葬了，并且要求村民们不能告诉吴秋月周大宝的葬身处。也不知道老村长哪来的消息，说吴秋月是克夫克村的灾星，但他的威望很高说的这些没有人敢反驳，甚至所有人都一副理所应当的样子。你帮着把周大宝葬了以后已经很晚了后就走了。

【拆迁第二天 2010年9月6日 阴】

周大宝这个人好歹对你有恩情，现在他的留下的遗媚日子不好过你总想做点什么，正在思考时你听见文香在门口大喊，张来富你个王八蛋去哪了。这个是你们的暗号，示意你今天随时可以去找她。你从芦苇丛里一路走到了文香家，缠绵过后你靠在床头抽烟，文香问你是不是有什么心事，你告诉她你想组织村民给吴秋月捐款，文香听了和你大吵一架后在一边生闷气，你见她这样就在上衣口袋里翻了翻，拿出一个金戒指给她，这是你很早就买好的戒指，你让她留下当做小玩意玩玩儿，以后你会补偿她一颗钻戒，文香的表情并不如你想象中的开心。

中午时你准备走了，阿芝这两天不知道去谁家混饭吃了，你得去找找看，毕竟这些天村里外人多。走的时候文香给了你两条中华烟，你拿了烟打算回家放好以后在村里逛逛，阿芝平时也喜欢到处跑村里人几乎都认识，她的安全一般不会出什么问题。在村里找了好一会都没找到阿芝，听人说前昨天还见着她，你想去找李丰收帮忙，结果去的时候村委的人说李丰收陪着付大龙去村里走访了，白天都不会回来，看来也只能靠你自己找了。回家晚饭后没多久，文香到了你家，你看文香那样子明显是被人打了，只是问她她也不愿说，会跑你这里来住，想都不用想肯定是张来富做的，你怒上心头，想着一定要好好教训张来富一顿。后来你趁着文香熟睡，偷了她的钥匙去到了她家。张来富这个混蛋在床上睡得正香，丝毫没有察觉你的到来，你狠狠的一脚踢在他的下体，看他惨叫着滚下了床，你迅速的离开了。

【拆迁第三天 2010年9月7日 大风】

你总觉得心里不安，平日里阿芝就算喜欢乱跑但最多一天就会回家的，现在都两天了还没回来可能是出了什么事情，李丰收那边不管用的话只有去找老村长出面帮忙，文香还没有睡醒，你起床洗漱好将饭菜给文香热好以后就出了屋。去老村长家中时你看见李丰收边穿衣服边跑出村委办公室，没一会吴秋月也跟在后面出来了，然后跟着出来的是衣衫不整的付大龙，这帮人到底是怎么一回事？不过现在管不了这么多了，找到阿芝是最重要的。

到了老村长家里把情况说明以后老村长让你放心，这个事情他一定会帮助你。你回去告诉文香让她好好在家里休息，而你继续寻找阿芝，下午时你找到王话梅家，你知道王话梅一直对阿芝挺照顾，平时阿芝也喜欢去她那里，到的时候你发现她和几个村里的老妇都在那里，屋子周围有几个村委和拆迁队的人，王话梅告诉你阿芝最近常常跑进山，你听了以后被吓得不清，阿芝一个傻姑娘山里实在是太危险，没有耽搁你立马离开了王话梅家往山上赶去。你在山里找了很久都没有找到阿芝，一直到深夜你在山上看见村子那边透出隐隐的火光，焦急之下急忙赶回村里。你到的时候众人都在忙着救火，起火的是村里的坟地。王话梅晕倒在一边不省人事，到处都找不到文香，阿芝也没有出现。后来你们在芦苇里找到晕过去的文香，如果发现得再晚一些她倒的那个地方就会被火势蔓延了。

当天的事：

【拆迁第四天 2010年9月8日 阵雨转暴雨】

昨天火势太大，整个村的坟山都被烧了，你也十分生气，毕竟你的父母也是葬在里面的，老村长全力调查这个事，所有的村民都不被允许离开，全部都要被叫到村委去问话，轮到你时已经是下午五点，而现在村里还没被问到的只有你、吴秋月、张来富、文香、王话梅几人了。老村长因为体力不支去休息了，调查的事情被交给了李丰收全权处理。

你的任务：
          
1、隐藏你和文香偷情的事。

2、选择合适的机会，帮助文香摆脱张来富的纠缠

3、查清阿芝的去向。

4、隐瞒你将张来富打伤的事

5、弄清楚其他几人在村子里面做过什么事情。

6、调查清楚村南坟山发生大火的原因

你对其他人的印象：

文香：有点小脾气但是对你十分温柔，想要共度一生的人。

张来富：你最讨厌的废物，没有之一，娶了文香但是没法给她幸福，他的奥拓车时刻提醒着你，这个人拥有文香不说还要吃软饭。

吴秋月：之前觉得这人没什么大问题，直到这俩天看到了某些画面后觉得周大宝可能头发已经绿成了村里的草地。

李丰收：以为读了俩年书就了不起，收管理费什么的根本就是以权谋私。现在想起他被你砸了的办公室和被你打的那个样子你都觉得爽快。

王话梅：虽然照顾阿芝，但是真的是个喇叭花成精了，只要没事就看见她在八卦。

【你是明白你自己有多英俊的，这些年还是有不少女人追着你，但是你从来不动心】

其他

文山：文香的父亲，去世的老支书，你很尊重他。

赵阿芝：你兄弟的傻女儿，这么些年过去你早就把她当做亲人，希望兄弟出狱后能够让他们父女团聚，为了保护她你没有告诉过任何人阿芝不是你女儿。

周大宝：曾经救过你，是个好人，仗义善良，这样的人才应该是支书。

付大龙：镇里来的坏胚。

吴大壮：王话梅的没用儿子，水平不好还爱赌，每次输钱就赖账，拆迁来的前一天零零总总已经差了你两千了。

