往事

20年前村里的人怎么说你的呢？分贝里出了个金凤凰！因为那时的你是村里唯考起了高中的知识分子。分贝村穷啊，穷到什么程度？来支教的老师往往是一到你们村感受到了村里的风就转身走的那种穷，遇见心好的还愿意在你们村里待个一年半载。怎么教书呢？整个村就一个班，五岁的孩子和十五岁的少年都在一起上课。一个老师什么科目都得教，村里人也不在乎山里娃能够认识几个字，种地才是最要紧的。

你的运气很好，十三岁那年来的支教老师在村里足足待了三年，负责任不说还特别的看中你，你在他的教导下成功的考取了镇上的高中，这可是村里头等的喜事。为了庆祝当时的老支书文山还特地摆了几桌。那可是风光无限，村里谁看了你不叫一声文曲星。你以为这样的风光会持续到你去到镇里，村里人的吹捧总让你觉得你是天生要做大事的人。去镇里之前你将自己的名字李狗蛋改为李丰收，毕竟要去镇里那不得先提前做做准备。

去到镇里以后你才知道，你的分数不过刚刚通过录取线，和镇里的那些孩子相比，你不光年纪大而且还十分的土气。班上的同学都瞧不上你，他们说的玩的你根本没有听说过，你学习不如他们快，高中的课程让你十分的吃力。你一门心思的想要融入他们但是从来都没有成功过，后来你遇见了一些镇上的混混，他们带着你吃喝玩乐让你见识到了镇上的生活是多么的精彩，也只有在他们之中你才能感受到什么是尊严。就这么混迹了三年，你原本以为你可以和那些兄弟一起在镇上干出事业，谁知道一次干架中，你其中一个兄弟下手太重把对方给弄死了。你们这帮子人坐牢的坐牢退学的退学，而你也被学校开除了。你尝试过在镇上生活，但一个高中都没毕业的人哪里活得下去。这个时候你才想起分贝的好处来，至少在那里你是备受欣赏的。路过复印店的时候你心思一动，花了三元钱在复印店做了一个假的毕业证。拿上毕业证的你就这么回到了分贝村。

回去后村里的人都十分的羡慕你在镇上生活过，常常拉着你问东问西。你虚荣心得到满足的同时也生出了一股优越感，至少和这些人比起来你是足够优秀的。村支书文山也在这个时候找到你，他让你去村委工作。他希望你这样的文化人能够带着分贝村发展，这样白来的机会你怎么可能放过，你答应了他的要求。因为你太年轻，文山还去找了不长露面但是威望很高的老村长来帮助你进入村委。

后来你工作出众隐隐的有取代文山的形势，你是个有野心的人怎么都不愿意他压在你的头上，没想到文山有次找你谈话，他告诉你不要想着出头，他能够让你起来也可以让你跌下去。原来他认识镇上高中的老师，他很清楚的知道你当年发生了什么事，你知道你能够在分贝村这么得人心不过是仗着你是个文化人。文山这个老东西这些年作威作福的也活够了，一次你趁着他在房顶抽烟，从背后一推将他弄下去摔死了。文山死了以后你在村民的支持下成功上位。

如今

这几年你大权在手，在村里捞到了不少的油水。每家交给村委的管理费都被提高，其中一部分会被你抽走一部分填满你的腰包，但是人都是越来越不满足的，村里待久了你又开始怀念镇上的生活，为了更贴近镇里人，你买了很多镇上人最喜欢穿的大牌，阿尼玛的西装、狗驰的皮鞋、时尚杂志等等，似乎只有这样才能提醒你，你和这个村里的人不一样。

你一直没有娶媳妇，村里的女人你一个都瞧不上。直到吴秋月嫁进分贝村，也不知道周大宝哪里来的好运气娶到了这样的美人。不过美是美了，吴秋月进村的时候可是大着肚子来的，这么不懂规矩的女人就算再好看也是要被村里的人说道的，果不其然平日里来找你说话的村妇们没少抹黑这个女人。不过吴秋月好歹是镇里的人，你俩的关系还算不错，你对她确实是多了一份心思，但这也不怪你，每次吴秋月看你的眼神都带着钩子要说她没那个意思你是不信的。

村里的人总是让你厌烦，一个个的都是不知好歹的。其中最麻烦的当属王话梅，这个女人年纪不过50辈分却是很高，平日里多嘴多舌，喜欢讲别人闲话但村民们又还听她的，有时候她说话比你这个村支书都管用。她年轻守寡带大了自己的儿子，前两年他儿子搞大了一个外村女孩的肚子，人家闹到了村委，王话梅撒泼打滚的硬要说不是自己儿子做的，如果不是你们村委帮忙瞒着处理了这个事，他早就被唾沫淹死了。还有当年文山死了后，分贝村忙着选新的村支书，王话梅女儿吴二妮就在那时候走失了，不过一个女娃子哪有时间管她，贴了几张寻人启事就这么过去了，这些年她都还没有放弃寻找，不过走失了这么久怎么还可能找得到。

文香也不省事，一直以为自己爹是村支书就很了不起，文山下葬的时候直接占了王话梅家的地，事情闹到了村支书，你们肯定是不好处理的，一个是刚过世的老支书、一个是村里辈分高的寡妇，最后要不是你们做通王话梅的工作，她哪里能够占到便宜，就这样文香还是不满意，真是白眼狼一只。
还有一个麻烦人物，赵瓦。这人是村里的棉花郎，以前离开过分贝村一段时间，回来的时候就带了个傻女儿，这人平日根本瞧不上你们村委。当初你提高村里管理费的时候这个泼皮直接带了一堆村民去你们村委闹事，你不答应他竟然砸了你的办公室，从此以后再也没有交过一分钱，甚至放话给十村八店的棉花郎，让他们不给你弹棉花，害得你一床子用了五年都没换。可是你又比较怕这种无赖，只能每次见他时狠狠的盯着他。

张来富这个人，总是喜欢和你攀比，硬是以为自己有辆奥拓车了不起，也不看看自己是什么货色，偶尔去他小卖部拿点东西，作为村支书你什么时候在村里付过钱，瞧张来富那个小气样子真的是没出息，每次被文香在屋里打得嗷嗷叫全村都知道，还非说什么自己是摔倒的，也不知道怎么摔才会把眼睛摔出熊猫的效果？

这几个货色都是不给面子的，平时你在家里摆酒请客，从来都不来，根本就没把你放在眼中。

最近

你收到镇里面传来的消息，分贝村要被拆迁了。如果拆迁成功，身为村支书的你可以被安排去镇上工作，这简直就是天降大饼，这些年你已经厌倦了村里的生活，正愁找不到方法可以去到镇上呢。果然消息下来没多久上头就派人下来了，你尽心尽力的接待只希望能够快点拆了你好离开这里，虽然你也知道村里有的老人对村子有很深的感情，但为了这个项目能够成功，你不介意使用一些手段。消息传出去以后吴秋月找到了你，那个时候你正在和村委的人商量要派谁带着拆迁测量队的人上山测量，带队的村民可以得到1000元的劳务费，吴秋月听见后眼睛都亮了，立马推荐周大宝去，周大宝经常上山也算是熟手你也就答应了，当下就给了吴秋月1000元，让她通知大宝明日一早进山。

【拆迁第一天 2010年9月5日 晴转多云】

你刚得到通知，如果分贝村拆迁成功，作为村支书的你可以被安排在镇上工作，得到消息的那一瞬间好像是天降大饼砸在你的脑袋上。还没等你开心够就有村民来告诉你周大宝死了。测量队一来村里就死了人，出这样的事拆迁项目说不定会被影响，跌下山谷只能算是意外，和拆迁队一点关系都没有。周大宝平时在村里为人很好，如果村民闹事就不好办了，当务之急是稳住吴秋月。你到的时候吴秋月正扑在周大宝身上哭得伤心，你赶忙上前扶起她，还没说话文香突然冲了出来开始打吴秋月。一边打着她一边骂她是个狐媚子。你和赵瓦上前劝架，怕把事情闹大你只能把文香拉着走远，赵瓦将吴秋月背了起来，其他人抬着周大宝的尸体离开了。

文香一路上都在骂骂咧咧，你不明白为什么她对吴秋月这么大的敌意，好在最后她答应你看在周大宝的面子上不再去找吴秋月的麻烦。等你回去的时候正好看见老村长叫人将大宝抬去埋了，说是吴秋月是灾星不能再让她见到大宝。付大龙得知出事后姗姗来迟，见到这个情景塞给你2000元让你拿来操办后事。因为操办简单总共就花销了五百多，现在吴秋月无依无靠，如果穷困到底线就只能靠着你过活，她在村里本来也就不受欢迎不如以后跟了你。

【拆迁第二天 2010年9月6日 阴】

一早付大龙就给了你八万元，说的是周大宝的赔偿费，毕竟是带路时出的事，公司不想把事情闹大于是申请了这笔钱，他让你把钱交给吴秋月。你满口答应看着这笔钱却心动了，上山死个人太正常不过，谁会想到有赔偿金呢。之后你就陪着付大龙去了王话梅的家中，付大龙知道王话梅在村里辈分高，希望可以劝说她拆迁起到带头的作用，到了王话梅家时他正在捞鱼，付大龙刚开口表明意见，王话梅就提着扫帚把你俩打了出去，付大龙丢了这么大的脸心里很是不痛快。他让你想办法教训教训王话梅，你也只能答应下来。整个白天你几乎都陪着付大龙走访村民，希望说服大家拆迁，只是工作进行得不算顺利。

晚饭时你路过文香家，看见张来富居然在打文香，这真是干年难得一遇，你在一旁看完热闹以后终于上前拉开两人并将文香护在身后，张来富见你来了没多说什么回屋把门锁了。你本来准备将文香带去村委会去将就一晚上，但她中途却说自己要回家你就随她去了，你在村委和付大龙喝酒喝到半夜，回家时路过王话梅家，想着早上被赶出来的事觉得十分的气愤，你偷摸着进了她的院子，走到鱼塘边看着这些鱼，这不是王话梅的命根子吗？没了这些看她不拆迁还怎么在村里活。说做就做，你走到一旁寻找发现地窖口旁的鱼饲料底下有两大袋生石灰，那是给鱼塘消毒用的，你将饲料搬开放到一旁，把生石灰拖了出来全部倒进了鱼塘里，倒的时候沾到水还不小心烧伤了手心，做完这些你就回到村委会与付大龙喝酒。

【拆迁第三天2010年9月7日 大风】

早上你因为昨天喝了不少和付大龙一同住在的村委会，付大龙这厮睡相不好，天，气又热你俩都只穿了裤视盖着被子躺在一起，还没等你清醒过来吴秋月突然闯了进来，看见你后她背过身告诉你王话梅要去镇上上访阻止拆迁，这个消息吓得你一个激灵，立马边穿衣服边跑出了办公室，你找上了几个村里的青壮赶去了镇上，好在抢在王话梅她们上访之前将她们带了回来，回村后你将这些人全部送到了王话梅的家中，并叫了一些人看管，回到办公室以后老村长已经等在里面，他面色很不好看，你知道最近村里发生的事让他很不满意，果然他告诉你如果这些事你处理不好他会带着村里的人，集体罢免你。

其实你知道老村长对你一直很不满意，他认为你只是利用这个村子而不是真心的对村子好，两年前他就想要扶持周大宝上位，如果不是因为吴秋月确实不受大家待见，说不定你已经被周大宝挤了下去，老村长训话结束后就走了。你越想越气觉得这个村子的人都不可理喻，好好的拆迁财不发，能够去镇上生活不去。他们既然在意自己的根那么就让他们没了根最好，反正这些人迷信，干脆就烧了村南的坟山，到时候再操作一下把他们吓走好了，反正只要拆迁顺利你就可以去镇上工作，不可能有假。今天正好是刮大风，方便做这一切，村里没有汽油到是村委后院停了一辆柴油拖拉机，你拿了一根管子看着周围没人走到了后院，打开拖拉机油箱盖，找了个桶放在地上，拿着胶管狠狠一吸，油没有出来，管子有点漏气。反复试了几次弄得面红耳赤的。最后一次用力过猛直接把柴油吸进了嘴里，还把你最喜欢的衬衣弄脏了，好在成功的把油弄了出来。

夜晚时你跑到坟山，将柴油洒在周围有芦苇的地方，正准备点火时突然听见远处村里的喇叭里传来老村长的声音，他在喇叭里一点脸面都不给你留，把你骂了个狗血淋头让你赶紧滚回村委，你不知道老村长发什么疯只能急匆匆的往村委赶。到了以后又是老生常谈，你只好拿出好酒好菜招待他，吃到一半突然有人闯进来告诉你们坟山起火了，老村长当时就怒了。你不知道为什么会起火，你和老村长一直在一起，到是可以被排除嫌疑。之后你们就赶往坟山那边组织灭火了。你当然不会真的上心做这个事，于是悄悄跑到远处躲懒，看见吴秋月朝这边走来，刚刚和老村长喝的酒冲上了大脑。你将吴秋月一把扯进了芦苇中，在她挣扎时将她敲晕在芦苇里强暴了她，之后你将她丢在芦苇里就走了，回到家中赶紧将弄脏的衬衣随便洗了之后就惊在了阳台上。

【拆迁第四天 2010年9月8日 阵雨转暴雨】

因为坟山起火的事情老村长十分暴怒，所有的村民全都不被允许出村，白天你同老村长一直都在调查，到下午五点还是没有找到嫌疑犯，没有被问话的人也只剩下王话梅、文香、赵瓦、张来富、吴秋月了。老村长年纪大了已经不太能支撑，他将调查的权利全部交给你，这事对你来说已经不重要，能够敷衍过去或者随便交一个出去都可以，不过样子还是要做的。

你的任务：

1、隐藏你在坟地浇油的事情，火势蔓延的这么快就是因为柴油

2、假意配合老村长调查村里的事情，尽量搅混水，把你做过的事嫁祸给别人。

3、隐瞒你强暴了吴秋月。

4、调查大家在村里面做了什么事情，如果发现别人的坏事，尽量把浇油的事情诬陷给别人。

5、你是村里支书，村里人要办理离婚都要找你做证明人，请根据你自己的想法，是否让他人成功离婚。

你对其他人的映像：

文香：十村八店最彪悍的女人，荣登分贝村男人最不想娶的女人NO.1，当年文山还想你可以收了她，还好你没答应，不然掉火坑的就是你。

王话梅：播音大喇叭，常常因为你没结婚和别人乱说你不行，但是辈分高年纪大又不好把她怎么样，教儿子不行撒泼打滚第一名。

吴秋月：镇里来的美丽女人，鲜花插在牛粪上，经常勾引你，但你没这么容易让她得手。

张来富：分贝村百年来最悲剧的男人，他的人生如果没有那辆奥拓车将是毫无意义，用一声告诚他人软饭没有这么好吃。

赵瓦：村里的刺头，不交管理费还理直气壮，简直是村里的一害，就这样还有不少女人喜欢。

其他：

周大宝：有运气的老实人，被吴秋月吃得死死的。

吴大壮：王话梅的废物儿子，无所事事游手好闲的无赖，搞大别人闺女的肚子不负责。

赵阿芝：赵瓦的傻女儿，平时还算听话，爱好就是满村跑。付大龙：拆迁经理，典型的镇里人，目前合作还算愉快。

文山：带你进村委的人，最后因为威胁你被你弄死。
        </pre>
