王话梅

分贝村的一天是由村里的广播开始的，伴随着村支书李丰收精心选取的歌单【妹妹看我、狼爱上羊、爱情买卖、香水有毒等】、村民们从沉睡中渐渐醒来，小卖部老板张来富已经出门准备开店，而老板娘文香还在屋内熟睡，王话梅在鱼塘边处理着鱼，饲料，她的儿子吴大壮刚刚从麻将馆回到家中打算睡上一整天。吴秋月正在哄着儿子周小贝起床，一家之主的周大宝已经下地在干活了。弹棉郎赵瓦刚刚起床正在洗漱，他的女儿赵阿芝推开房门跑了出去在芦苇地里开心的笑着。

地点：分贝村 总户数：50户
姓名：王话梅 性别：女 年龄：50 职业：村民兼职养鱼
房屋面积：550 平米（无装修，较为破旧但历史年份较高，无考古价值）

往事

分贝村就是你的根，从出生到嫁人你都活在这个偏僻的村中，你的父母长辈靠着这里的土地养大了你，而你也同样在这里养大了自己的孩子。你从没有想过要离开，只想在剩下的日子里，守着这里的山水直到你也化成黄土。分贝村向来穷苦，一年的收成几乎决定了来年的生计，除了种地以外家里还有一个小鱼塘，卖鱼是家里的大部分收入。

20年前你儿子吴大壮才5岁，丈夫就因为生病骤然离世，剩下你们孤儿寡母的在这村里，那时你的肚子里还怀着他的遗腹子，好在你能够下地干活、家中又有鱼塘的收入作为补贴，日子也还算过得去。后来你生了个女儿，取名吴二妮。想着死去的丈夫，你发誓要把这一双儿女养大，哪怕拼了命也要保护他们。

可惜15年前你的女儿跑出去玩耍后再也没回来，你在村里面找了几天都没有音讯，无奈之下只能去找当时的村干部李丰收帮忙，没想到那个时候他忙着选村支书，草草的定为走失后就贴了几张寻人启事，之后再也没管。村里走丢了人，还是个女娃，这辈子你可能再也见不到她了，只能收藏着一张寻人启事当做最后的纪念，不仅如此你从来没有放弃寻找过她，每次去镇里卖鱼都带着这个寻人启事一次次的询问着别人。失去女儿以后你对儿子越发溺爱，等你意识到的时候，吴大壮已经长成了一个游手好闲的无赖，你常常揪着大壮就是一顿打，但没有任何作用。可这又有什么办法，他是你唯一的儿子，是吴家最后的血脉，你只能护着疼着。

村里的人不多，大部分小辈都是你看着长大的，其中周大宝是你最心疼的一个，他父母去世得早，小小的孩子在大家的帮衬下长成了男人。周大宝很有孝心从来没有忘记过你们的帮助，在农忙时常常主动帮忙，和自己儿子比那叫做一个懂事。五，年前周大宝突然离开了村里去到了镇上，回来时带了一个叫做吴秋月的女人，大着个肚子听说还是镇上的人，瞧着就不像什么好人家的闺女，一脸媚相。周大宝给大家说这个是他的媳妇，村里的女人都不是很喜欢她，而且吴秋月也十分高傲，平日里见了你们也不打招呼，但周大宝就是喜欢她，像是中了邪一样。

如今

到了这把年纪，日常除了和村妇们八卦也没有其他的乐子。如果要说烦心事的话那就是你儿子总是沉迷赌博了，大壮随了他爹白长这么大个，脑子却笨得很，平日里混在麻将馆的都不是省油的灯，大壮常常输得一干二净的回家找你要钱，最近更是疯魔，常常整夜整夜的不回家，去麻将馆也找不到人，不知道是做什么去了。他平时很是喜欢跟着赵瓦，言语间对赵瓦的钦佩让你觉得十分的无奈。

赵瓦也是分贝村的老村民了，年轻的时候仗着一张帅脸勾搭了不少姑娘，虽说弹了一手好棉花但确确实实的是个小痞子。后来他去镇上打拼几年后回来就带着个傻女儿阿芝，这个王八蛋只管生不管养，平日里要是没你们这些女人帮衬着恐怕阿芝早就死了。你因为走失的女儿所以平时对阿芝格外的照顾，她也十分乖巧只要给一颗冰糖就会乖乖听话的在一边玩，从来不会打扰别人。这些年照顾下来你几乎是把她当做了亲闺女，村里人都叫阿芝小哑巴，但你知道阿芝只会和亲近的人说话。

文山下葬时文香竟然占用你家祖坟的地，你闹到李丰收那个混蛋那里去，他不管不说还强行逼你同意，每次看见文香那个得意的脸嘴你就气得肝痛。作为报复你总喜欢在村里妇人之间笑话文香，不过也不是造谣，毕竟文香不能生这个事可是你们一直当做笑话来说的。

李丰收这些年在村里各种敛财，什么收取管理费啊，摆酒席啊，无所不用其极，后来有一次竟然伙同外村人诬赖你儿子，说你儿子弄大了别人闺女的肚子，这怎么可能，到最后他看骗不到钱不也不了了之了吗？

周大宝这些年和你的来往渐渐的少了，虽说还是经常来帮忙，但人变得不爱说话，他那媳妇当初生了个儿子，这孩子三天两头的生病。吴秋月嫁来以后从来都不下地，家里的活计就落在周大宝一个人的身上，她就在家里带带孩子什么的。别看吴秋月平时不怎么搭理你们，她倒是和李丰收关系不错，两人常常有说有笑，你提点过周大宝几次，但他只是沉默着也没有多说什么，你看他执迷不悟也就不想管了。

最近

李丰收毕恭毕敬的迎来了一堆城里人，说是什么大公司看上了分贝村，准备把这里拆迁了。拆什么拆！你绝对不同意！你生是分贝村的人，死了也是分贝村里的魂！你家的坟、你家的地都在这个村子里，想把你赶走门都没有！消息刚出来你就和村妇们一起商量着这事，听说吴秋月得到了消息后就去了李丰收的办公室，出来以后满脸欢喜的，她说是周大宝会给拆迁测量队的带路，还得了1000元钱。1000元那可是不少了。

晚上时大壮跑回家，说是欠了赵瓦2000元，你问了才得知他这段时间打牌一直，输，每次都写欠条，现在欠了2000多赵瓦让他还钱了。周大宝上个山就能挣1000块，而大壮随随便便就把你半年的血汗钱输了出去。你气急了狠狠地打了大壮一顿。那2000你是不打算还了，谅赵瓦也不敢来找你，这种小痞子能够赢钱绝对是出老千

【拆迁第一天 2010年9月5日 晴转多云】

中午时分，你刚做完农活准备休息吃午饭，突然看见吴大壮跌跌撞撞的跑回家，见着你的第一句话就是“死人了”。你一头雾水不知道发生了什么，只是看着大壮这惊慌失措的样子心里十分着急。你叫他冷静下来，他缓了一会后告诉你，他把周大宝杀了。你当下脑子一乱几乎没有反应过来，你一耳光打向了大壮并悄声问他到底是怎么回事，他说他和周大宝因为吴秋月起了争执，他不小心将周大宝踢下了山谷。你就知道吴秋月这个女人不是个好东西，一定是她蛊惑了你的儿子。现在事情已经发生，你再心疼周大宝但也不能不管自己的儿子。当下只能先把吴大壮送出村外，你急忙收了一些衣服带着吴大壮偷偷离开了村子，离开时看见文香和李丰收拉拉扯扯的走着，你和儿子赶紧躲进了一边的芦苇地里。大壮上车前你给了他一些钱，告诉他，去镇上先待着，等过两天你会去平日镇上赶集卖鱼的地方给他送钱，让他到你常在的摊位等着。

【拆迁第二天 2010年9月6日 阴】

儿子在镇上需要生活费，上午一醒来你就开始在鱼塘里忙活，希望能够弄到足够多的鱼去卖钱，卖鱼的费用加上家里剩下的钱应该够他在镇里面生活一段时间，正在忙碌时付大龙和你李丰收来到了家中，他希望你能够作为村里的代表说服大家拆迁，你怎么可能同意，当下就拿着扫帚把付大龙赶出了门。下午时你收到一封匿名信，写信的人知道周大宝是被吴大壮弄死的，要求你给五千元的封口费，并埋到村南坟山周大宝的新坟后。

被人抓住了这样的把柄你哪里敢拒绝，只能将家中所有的钱拿出来，那都是五元十元的零钱，是你卖鱼好不容易攒出来的。你照着他的要求埋到周大宝的坟墓后就立刻离开了。毕竟自己的儿子害了他一条命，你又帮着隐瞒怎么可能不心虚。好在家中还有鱼塘，明天把鱼卖了以后还是能给儿子弄出足够的钱。回家时你远远的看见张来富在打文香，下手还不轻，文香的痛叫声都要传到你家中了。如果是以前你一定好好宣扬一番，但是现在哪里还有这个精力。凌晨两三点时你听见家中有动静，前去查看发现家中挂在的香肠被扯断了几根，估计是被野猫叼走了，这种事在村里经常发生你也就没有多管。睡得迷糊时你隐隐约约的听见了哭声，难道是周大宝的鬼魂回来了？

第二天你起来想着都一阵后怕。

【拆迁第三天2010年9月7日 大风】

你准备今天把鱼塘里的鱼全部捞去卖了，心疼儿子的你只能把自家鱼塘里的小鱼苗全部捞来卖了。谁知早上拿着卖鱼所需要的东西，来鱼塘时发现鱼全死了，你看鱼塘旁边有两袋倒光了的生石灰，这个是用来给鱼塘消毒的，因为平时不常用，都是放在鱼塘边的地窖口旁，而且被压在几大袋鱼饲料下面，现在鱼饲料已经被放到了地窖口上，而下面少的生石灰就是被倒在鱼塘里的那两袋，看样子是有人故意要害你。这下子你所有的指望都没有了，村里人就算有再多的矛盾也不会动人活命的营生，能够这样做的一定是拆迁队，你心里气不过准备叫上一些村里和你关系好又不愿意拆迁的老村民一同去镇里上访。

没想到刚到镇里就被李丰收带人拦了回来，并且安排了几个人看住你们，期间赵瓦过来了一次，他的女儿阿芝失踪了。这些年你也很照顾阿芝，几乎把她当做半个女儿，虽说阿芝是个傻子但十分听话懂事，现在不见了你心里也着急，你想起这段时间常常看见阿芝从山里高高兴兴的出来，于是告诉赵瓦让他去山里找找。夜晚时村里的大喇叭突然响了起来，老村长在喇叭里将李丰收骂了个狗血淋头，语气不善的让他赶紧滚回村委。半夜时村南的坟山起火了，火势很大几乎所有村里人都过去了，因为村南风水最好，村里人去世后几乎都是葬在那里，你家的祖坟当然也在那里，你赶到时火势已经不能控制了，所有的坟全部陷入在了火场之中，老村长在一旁气急败坏的组织着救火，你看着自己祖辈的葬身之地变成这样，心里一急眼前一黑晕了过去。

【拆迁第四天 2010年9月8日 阵雨转暴雨】

醒来时，你发觉自己是在家里面醒过来的，已经是第二天的下午，一番洗漱后，还没等说话你就被带到了村委，老村长因为这个事十分的气愤，说是一定要严查，村民全被组织起来问话，你、文香、张来富、赵瓦、吴秋月是最后的五人，现在老村长因为体力不支去休息了，这件事被全权交给了李丰收处理。

你的任务：

1、隐藏儿子杀死周大宝的事实。

2、调查大家在村里面做了什么事情。

3、查清自家的鱼被谁弄死的。

4、调查清楚村南坟山发生大火的原因

你对其他人的印象：

文香：不会生孩子且爱打老公的女人，是平时你们最喜欢八卦的对象。

吴秋月：狐狸精，镇里来的坏女人，是个男人都想勾引，村里女人的公敌，也是平时你们喜欢八卦的对象。

李丰收：原名李狗蛋，自从当上支书就膨胀了，多年没有娶妻，初步被怀疑是不行。还是你们喜欢八卦的对象。

张来富：文香的老公，因为有他的存在才衬托出别的男人有多幸福，嫁人如此不如守寡，你们甚至都不想八卦他。

赵瓦：小痞子，祸害了多个妇女，村里女人和丈夫吵架多半是因为他，也是因为他你们才有了更多的谈资，但不得不说棉花弹得很好，是十村八店弹棉郎的精神领袖。

【是的，没错你是真的很喜欢八卦，毕竟这个村里除了八卦还有乐趣可言吗？】

