往事

都说红颜薄命最是可怜，你却觉得那未尝不是一件好事，红颜命苦还命硬才是真的凄凉。你时不时的还是会梦见年少的时光，永远充斥饥饿与寒冷，昏黄的灯光下父亲冷漠的脸，像是被水淹没的感觉令人室息却无法逃离。好事的邻居说你和你娘都是狐狸精转世，你还在祸褓时她就丢下你们父女和别人跑了，听说是去当了有钱人的小三，你当然不愿意相信，可却又无法辩驳毕竟你从来没有见过她，也从来没有过一个完整的家。

16岁那年你的父亲喝醉了以后就在屋里乱砸东西还对你发脾气，你不能忍受于是跑出了家门。屋外下着大雨你头也不回的向远处跑去，雨声中你听见父亲在叫着你的名字，可是就算回去了又能怎么样呢？思绪慌乱之间你撞上了一帮混混，看清你的脸以后他们就这么拦着不让你走，有几个甚至上手拉扯你的衣服，你吓坏了只能大声的叫着爸爸救我。当被这帮流氓按在身下时你想你完了，这时你的父亲突然出现推开了混混将你护在怀中。这些混混哪里愿意，很快就和你的父亲扭打了起来，父亲一个人哪里是这么多人的对手，没一会他就倒在了血泊之中，混混们看事情闹大了全都跑走只留下你们父女。你上前将父亲抱在怀中一边拖着他走一边求救。在这大雨倾盆空无一人的街上父亲死在了你的怀中。

父亲下葬时你才知道，你的父亲原本也是个普通且幸福的男人，有一个稳定的工作和贤惠且美丽的妻子。当地的黑社会看上了母亲强暴了她，父亲不过是个小人物根本没办法保护好她，原本这事过了母亲也已经走出来了，谁知道她怀了你。你是那个男人的孩子，如果不留下你，母亲可能再也不能怀孕，于是他们俩决定生下你，可你出生以后母亲就跳河自杀了，至今没有找到尸体，而父亲也崩溃了。你是母亲唯一的孩子也是他们仇人的孩子，你不知道父亲用什么样的心情养大你，但这份恩情你是再也没机会还了。那些打伤你父亲的混混过了很久都没有被抓到，黑夜中你看不清脸，只记得其中一个年纪大点的人手臂上有个很特别的太阳纹身。

父亲走后你的生活陷入了困境，家里本来就山穷水尽，父亲没有什么朋友可以帮衬房子也是租的，你只能退了学去打零工维持生活。

22岁时你靠着打零工攒的钱在集市弄了一个小摊位卖一些吃食，很多周围的混混流氓都常常来骚扰你，大家都觉得你是自己搞出的这些事，所有人看你的眼神都带着调侃和鄙夷。你尝试过解释但是却没有人相信，后来你遇见了周大宝，这个男人时不时的会到集市卖一些野味。第一次见面以后他就常常出现在你的周围，不仅帮你赶走了流氓还常常帮你做事，你看得出来他是真的喜欢你而不是想要占你便宜。一次你收摊后被流氓缠上，也是同样的大雨，那些回忆让你惧怕不已，最后是周大宝冲出来保护了你，你看着他的背影想到了自己的父亲，看着受伤后还护着你的周大宝你想或许终于遇见了可以依靠的人。

你和大宝刚在一起时是一起生活在镇上的，你知道他是分贝村的人，很穷但你在镇子上也不过是苟且偷生罢了，怀孕之后他希望你和他回到村子里，因为就凭你们俩的能力是没办法多养活一个孩子的，只要他在你觉得哪里都是家，于是毫无留恋的和他来到了村子里。进村时肚子已经大了，村里人看你的眼光总是带着审视，但这也不重要了，更恶意的你都遇见过，后来你给他生了个儿子叫做周小贝。

如今：

小贝出生后身体一直都不好。现在已经四岁了还是瘦弱得很，为了给他治病你和周大宝花了不少钱，好在大宝除了种地以外还会上山里打点野味去镇上卖，而你除了带带孩子之外只能够做点不耗费体力的活计。

大宝在村里的人缘很好还是老支书文山的干儿子，只是他们大多不怎么喜欢你，慢慢的大宝和他们的往来也少了。这几年你不常出门对村里的人了解也不多，能够知道的只有那些个风云人物。

文香，大宝的干姐姐，因为在村里开了一个小卖部算是村里的富户，不过性格牌气非常暴躁，常常在家里打她老公张来富，好几次你路过小卖部时都看见张来富鼻青脸肿的。张来富这个人非常的死要面子，每次去进货的时候都开着他的奥拓车，像是炫耀一样只要开出来就会一路按着喇叭直到出村。但你在镇里待过是知道的，他的小卖部全部是假冒伪劣产品，也不知道是跑去那个旮晃里找的货源。

王话梅是村里的老村民了，听说是年轻时就守寡了，独自养大了自己的儿子吴大壮，只是他的儿子不怎么争气，常看见她怜着吴大壮就是一顿凑。

赵瓦是村里的棉花郎，养着一个傻女儿阿芝。大宝同他的关系还算不错，你们家特别困难的时候他偶尔会送点东西过来，你常看见村里的中年妇女偷偷的看他。其实你之前见过他，大约八岁的时候在镇医院，你看见还年轻的赵瓦在医院大楼里哭得很是伤心，因为他长相出众所以你记得很清楚。

李丰收算是唯一一个你在村里能够聊得来的人，他是镇里高中毕业的，为人处世还是和村里的人不一样，你能够感觉到这个他对你有好感，但你只是把他当做朋友。但奇怪的是以他的条件怎么可能这些年都没结婚，隐隐有传言说是他不行，不过这种事你也不好问别人。

这段时间小贝又病了，家里最后的钱全都投进了医院里。你们两夫妻都是一筹莫展，快揭不开锅了。不过只要有周大宝在你就有主心骨，你相信日子会越过越好的。

最近

5天前，李丰收毕恭毕敬的迎来了一堆城里人，说是什么大公司看上了分贝村，准备把这里拆迁了。拆迁代表什么？代表着你们可以得到不少的钱啊！小贝的医药费有了着落，拿着这些钱你们也可以回到镇子上生活。你俩计划着未来，满心都是懂憬。你去找李丰收打算多打听这个事的时候他正在开会，你听到他们说明天要派人跟着拆迁测量队的人上山，还有1000元的劳务费。1000元可是不少了，小贝还等着钱买药，周大宝经常进山只是带队那么再合适不过了。你把大宝推荐给了李丰收，李丰收倒是答应得很爽快还把钱直接就给了你。你拿着钱高兴的回去找了周大宝。

【拆迁第一天 2010年9月5日 晴转多云】

大宝临走时还告诉你他会尽快回来，因为这段时间你在家里总觉得有人看着你，家里的东西总会莫名其妙的消失，大宝不在你还是会觉得有些害怕。中午时你还在做饭，特地去买了三两肉炖着，想着大宝辛苦、等他回来了得多吃一些。刚摆好碗筷就看见赵瓦进来，急匆匆的告诉你周大宝出事了让你赶紧去看看，你跑过去时就看见一帮人在那里围着，你拨开人群朝里走去。那是一个血肉模糊被草席盖着的人，颤抖着掀开席子看见了满目的血。

是大宝，还穿着上午你给他新拿出的衣服。你喊着他的名字他却是静静的躺在那再也没有办法回应你。你终于忍不住扑在他身上哭了起来。李丰收在一边拍着你的背安慰着你，接着你被一股大力拉了起来，文香撕扯着你骂你是狐狸精，你任由她打着，后来是李丰收和赵瓦上前把你们分开，之后你就晕了过去。醒来时已经是晚上，只有小贝和一个村妇守在你的旁边，大宝则是被直接下葬了连个交代也没给你。你给小贝热了饭菜，看着属于大宝的碗筷心中满是绝望。晚上小贝发起了高烧而你照顾了他一夜，这是周大宝和你的孩子，你发誓一定要把他照顾好。

【拆迁第二天 2010年9月6日阴

一早李丰收就来找你，给你说周大宝的后事已经安排好了让你不用操心，放下了一些吃穿用度的东西后就走了，临走前告诉你有什么事可以去找他。大宝是你的丈夫，后事你怎么可能不管，你正准备追上前问清楚时就听见孩子的哭声。你不得不回到房间，小贝浑身滚烫意识不清。当下肯定是孩子最重要，你抱着小贝准备去镇里的医院看看。村里去镇上的班车很远，如果是走的话要走一个小时。
你听到熟悉的喇叭声知道是张来富要去镇里了，回头一看果然是那辆熟悉的奥拓，你拦下了车求张来富送你去镇医院，好在他答应了。到了医院张来富非常热心的帮忙，跑上跑下的开着单子。小贝的病情需要住院，光是费用就要五干，你手里哪里有这么多钱，一时间慌乱得不知道怎么做，张来富却是安慰着你并积极地跑去把医药费交了，随后把单子一股脑的塞给了你，借条也没让你写。

你原本想留在医院陪着孩子，但不管是拆迁的事还是大宝的事都不允许你留下，你用最后的钱请了一个护工帮忙看顾，随后就坐上了张来富的车准备回村，这时已经是下午了。回去的路上你因为太过疲惫睡了过去，中途感觉到车子停了下来，你迷迷糊糊的睁开眼时看见张来富提着一个桶往车这边走来，随后你就睡过去了。快到村里的时候张来富把车停了下来，随后他将你抱住想要对你动手动脚，你拼命挣扎后跑下了车。

你回到家后躲在屋里不敢出门，晚上十二点时听到外面有动静，大着胆子看了眼发现是赵瓦的女儿阿芝在你门口。周大宝在时对阿芝很好总是很照顾她，你把她带进屋里看见她脏兮兮的样子就去给她打了一盆水清理一下，你给她清理头发时刚拿上剪刀准备给阿芝修剪指甲的时候，似乎注意到阿芝的脖颈处居然有一道咬痕，难道是有人侵犯过阿芝？你正准备将阿芝的衣服褪去检查她身体的时候，她突然对着你说了句“周哥哥下去了，推下去了”。听到这话你心中一紧，立马抓着阿芝大声的问她是不是见到了什么。阿芝大约是被吓到了，挣脱你后跑了出去，你剪刀都来不及放下就这么追了出去，你喊着阿芝的名字但她始终没有回头快速的跑进了芦苇地。大宝的死一定有问题，而阿芝一定是知道了什么，明天一定要找到她问清楚才行。

【拆迁第三天2010年9月7日 大风】

上午一早你去镇里给小贝送东西，毕竟还不知道要在医院住多久，离开村时听见有人悄悄谈论说是王话梅准备组织人去镇里上访阻止拆迁，你哪能让这种事发生，于是急忙跑到了村委会，打开门冲进去看见李丰收和付大龙两人赤裸的在床上抱在一起睡着，你赶紧背过身，这时李丰收已经醒了，你将事情告诉他后，他一边穿着衣服一边跑了出去，而你也不好留在这里只能也离开了。

到了医院时医生告诉你小贝得了胃癌，光是治疗都要花25万，你哪里拿得出来。哪怕是拆迁了都不够，大宝已经去世了，如果小贝有什么事你哪里对得起大宝，无论如何一定要凑到足够的钱，不管付出什么代价，回到村里后你到处打听大宝的坟墓在哪，但是没有一个人告诉你，那些上了年纪的人看你更是像看什么脏东西一样，恨不得离得远远的。

一直到晚上你才打听到大宝在死的那一天就被葬入了坟山，期间听到老村长在村广播里面骂着李丰收，但你也没仔细听。你拿着买好的香烛纸钱准备去坟山祭奠他，到了山上以后找了很久才找到大宝的墓，墓地十分简陋但好在看得出来有不少人来烧过纸钱，你给他摆好贡品，那是他那天没来得及回家吃的炖肉。想着这几天的事你忍不住泪如雨下，烧完纸钱以后已经很晚了，你慢慢的走在回家的路上。

走到芦苇地时你看见周围的山上起火了，火势很快的蔓延开，这不可能是你烧纸钱造成的，那个地方全是泥土石堆根本没有可以燃烧的东西，那为什么会这样。村民们渐渐聚集，你原本也打算跟着上去救火，但是被后面赶来的人推了一把跌倒在地，扭伤了脚。大宝可还在那里，你心急如焚却因为脚伤落在了大家后面，正着急时你被一股大力扯进了芦苇，接着头上一痛晕了过去。

【拆迁第四天 2010年9月8日 阵雨转暴雨】

醒来时已经是第二天下午，你衣衫凌乱后背刺痛，你知道你被人强暴了，只是现在你又能和谁诉苦呢？你穿好衣服摇摇晃晃的离开芦苇地，刚离开就被村民抓住了，说是老村长要调查坟地纵火的事情，他们找到了你很久了，接着你就被带到了村委。因为昨天的事老村长很是生气，他让李丰收查清失火的真相，而现在整个村子还没有被问话的就只有你、文香、张来富、赵瓦及王话梅。

你的任务：

1、查出周大宝的死亡原因。

2、查出是谁强暴了你。

3、阿芝似乎被别人侵犯过。连阿芝这种女孩都不放过，你一定要找到这个侵犯阿芝的人。

4、尽量调查清楚其他人在村里面做过什么事情。

5、调查清楚村南坟山发生大火的原因

你对其他人的印象：

文香：周大宝的干姐姐，对你很不友好，是个彪悍的女人，可惜40了还没孩子。

李丰收：和你关系不错，你感觉他喜欢你，但这两天发现他不娶老婆可能是另有隐情。

王话梅：虽然你不和人接触，但是你知道村里很多关于你的流言都是这个女人传播的。老不正经不好好教儿子天天八卦。

张来富：有贼心没贼胆的窝囊男人，开着一辆你见过最破最脏的奥拓，守店的时候喜欢在小卖部里面枢脚，扣完脚经常用钥匙上的水果刀削苹果吃，真是把你恶心得够哈。

赵瓦：村里的颜值戴峰代表，对你们家还算是照顾，经常送东西过来。

【你有八百度的近视，五米之外分不清是人是狗，所以为了避免认错人，你从不和别人打招呼】

其他

