张来富

分贝村的一天是由村里的广播开始的，伴随着村支书李丰收精心选取的歌单【妹妹看我、狼爱上羊、爱情买卖、香水有毒等】，村民们从沉睡中渐渐醒来，小卖部老板张来富已经出门准备开店，而老板娘文香还在屋内熟睡，王话梅在鱼塘边处理着鱼饲料，她的儿子吴大壮刚刚从麻将馆回到家中打算睡上一整天。吴秋月正在哄着儿子周小贝起床，一家之主的周大宝已经下地在干活了。弹棉郎赵瓦刚刚起床正在洗漱，他的女儿赵阿芝推开房门跑了出去在芦苇地里开心的笑着。

地点：分贝村 总户数：50户
姓名：张来富 性别：男 年龄：36 职业：小卖部老板
房屋面积：330 平米（精装修，与妻子文香共同拥有）

往事

来富，是你全家三代的希望，出生的时候家里连饭都快吃不上了，所以你爹给你起了这么个名字，希望能够改变自己的命运。你们家是当地有名的贫困户，而你则是第五个孩子，前面的四个不是病了没钱医治就是营养不良死了。生下你以后你爷爷奶奶就去世了，他们走到了山里再也没有回来。因为少了两张吃饭的嘴，你成功的长大了，但家里的情况一直没有变好，日子苦得让你不愿意去回想。

20年前你才16岁，你爹生了很严重的病，只能在家里吃着山里挖来的药，村里的老人说这样的病得花大价钱才能治好。那时的你因为营养跟不上，瘦得跟什么似的。你爹要是走了你和你娘日子估计也过不下去了。也就在那个时候，隔壁分贝村村支书一文山，要找上门女婿的消息传到了你们家，五万元的嫁妆，并且不要一分钱的彩礼，五万元对你们来说简直就是笔巨款。不仅能治好你爹的病，还能够买两年的粮食。只是在村里，上门女婿都是被人看不起的，所以哪怕条件很丰厚也没人愿意做。

不过别人是别人，你们家这个情况，这是唯一的出路了。

得到消息的第二天，你娘带着你，提着家里最后的鸡蛋，跋山涉水的走到了分贝村，到的时候已经晚上了，好在文山在家。他看到你以后皱了皱眉头，你娘见他这个样子立马就跪下了，而你在旁边木讷着说不出一句话。不知道什么打动了文山，他同意了这门亲事，而你就被留在了这里。送你娘去村口的时，她不停的说着对不起你，并且告诉你一定要对媳妇好，毕竟这五万的嫁妆救了你爹一条命。你倒是觉得无所谓，能活着不也就挺好的吗？后来没几年村里闹瘟疫，而你的父母都死在了那场灾难之中。

你在文山家住了下来，但是文香却没有出现，文山告诉你她去镇里亲戚家住一段时间，等她回来你们就成亲。你的身板做不了活，只能在村里闲着逛逛，那个时候听到村妇们闲聊，你才知道，文香是村里有名的泼妇。脾气差并且不安分，要不是家里有点钱这样的女人是嫁不出去的。你想着正好白捡个媳妇，虽然说比你大四岁，但过日子嘛，能凑合就差不多了。谁知道这一凑合就是半生的凄苦。

和文香结婚一年后你们俩就分房睡，她看你时从来没有好脸色，他爹活着的时候还好，死了以后文香几乎隔几天就要找理由打你一顿，想着她是女人，你也不好动手。不管在外在内她从来都不给你留脸面，但离了这里，你也没地方去，只能这样苟且偷生。你们结婚多年都没有孩子，直到五年前你翻到文香的体检报告才知道文香不能生，哪怕你只是个上门女婿，传宗接代也是大事。村里生不出孩子的女人会被人看不起，为了堵你的嘴，文香给你买了你梦寐以求的奥拓车，这是村里唯一的一辆小轿车，你想着也只能这样过了，每次开车出去进货的时候你都要把喇叭从村里一直按到出村，似乎只有这样才能发泄你心中的不满以及那一丝虚荣。

你和文香几乎没话可说，平日里因为你不能干农活什么也做不了，都是被她打发去小卖部守店，她一个人在家落个清闲。文香这个女人贼得很，烟酒这种贵重的东西，你拿回来了以后都是交给她，需要买烟的人都是直接找文香，你是从来不吸烟的，刚结婚的时候你曾经偷偷尝试过，因为身上有烟味被文香给打了，你也不知道这个女人什么毛病，自己不吸烟也不准别人吸。

如今

距离你和文香结婚已经19年了，36岁的你孩子没有半个，老婆有了不如没有。家里的财政大权掌握在文香的手中，离了她你是身无分文，平日里只能靠捞一点进货和卖货的油水过日子。正值壮年的你老婆不让碰，村里的女人都惧怕文香，更不会搭理你，出去找吧，又没有那个经济实力。

这种情况下，你看上了赵瓦的傻女儿赵阿芝，赵瓦以前是村里的棉花郎，去镇上打拼了几年后就带着这么个傻女儿回了村。阿芝不光傻还是个哑巴，平日里很乖巧，村里的女人拿一颗冰糖就能把她哄得服服帖帖的。于是你就拿着店里的糖诱哄她，为了避免村里人发现，你都是把她带到山里行事。也怪赵瓦平时嫉妒你有钱爱找你麻烦，有次你不过就骂了他一句穷酸，他居然把你的车胎给扎了，你存了半年的钱才把四个轮胎补好。

李丰收虽然是村支书，但是良心是坏透了，常常在你的小卖部拿了东西不给钱，为了敛财常常屁大点事都要摆上几桌，每次都要叫上你，你哪里有钱送礼所以从来都不去。

这些年你手上慢慢有点小钱，每次进货扣下来的钱都被你存入存折里，十多年了私房钱一千五百多，也是一笔巨款了。不守店的时候就喜欢偷偷去村里的活动室打打麻将，活动室里面最不受欢迎的就是吴大壮，赢了就跑输了赖账，仗着人高马大，不少惹村里人闲话。不过她妈王话梅是村里的老人，年轻的时候就守了寡，拉扯着儿子长大不容易，大家看在她的份上倒不会多说什么，只是王话梅这个人舌头很长，没事就喜欢和一堆老女人嚼舌根，她们家在村里条件还不错，家里有个小鱼塘，靠着养鱼还有点收入，很多年前她女儿吴二妮走失了，李丰收这个不干人事的忙着选支书根本就没怎么管，倒是王话梅还没有放弃寻找。

这几年如果非要说有什么值得注意的，那只有周大宝的老婆吴秋月了，一看那个气质就不是村里的女人比得了的，而且这个女的大着肚子进的村，你们这些男人私底下都说那一定不是周大宝的种，周大宝除了能够上山打打猎以外没什么出众的，就是个老实男人。只可惜这个吴秋月看起是个勾人相，却不怎么搭理你们。不过她们家条件不好，每次吴秋月路过小卖部的时候都是一副奇怪的模样，你怀疑她可能想偷小卖部的东西。

最近

5天前，李丰收毕恭毕敬的迎来了一堆城里人，说是什么大公司看上了分贝村，准备把这里拆迁了。听到这个消息，你突然觉得人生充满了希望，只要把房子拆了，那些钱一定有你的一份，拿钱了把婚离了，去镇上找个年轻女人结婚。就可以脱离苦海了，这几乎是你开始新生活的唯一办法。拆！赶紧拆！

【拆迁第一天 2010年9月5日晴转多云】

周大宝死了。

本来吧，今天是个好日子，上午你在小卖部看见阿芝后给了她一颗糖，之后就将她带到了山里。正在行事时突然听见争执的声音，你吾住阿芝的嘴让她不要出声。前去查看发现是周大宝同吴大壮在吵架，吵着吵着两人打了起来，那叫一个飞沙走石日月无光，吴大壮人如其名的壮硕，周大宝因为常常上山打猎十分灵活，这两人你都不待见，于是乐得在一旁看他们狗咬狗。本来只是一场寻常村汉之间的争斗，谁知道他们打架那地势不好，正在山谷旁边，吴大壮混乱中一脚将周大宝踢下了山谷，之后就匆匆的跑了。

吴大壮走后你前去山谷边查看，没想到周大宝命大没死，挂在了山谷旁的树上后爬了上来。还没等你们说话就看见周大宝瞪着你的后方，你转头一看发现阿芝赤身裸体的跟了上来，再看看自己衣衫不整的样子，这事要是被桶出去了，你肯定没好果子吃，于是你将刚爬上来的周大宝又推了下去，之后你急忙让阿芝穿好衣服。你俩分开下山。回家后你想着周大宝心中一阵后怕，晚上时还梦见周大宝找你索命，后来被文香一脚踢下了床才清醒。

【拆迁第二天 2010年9月6日 阴

一大早文香给了你3500块，就把你打发去镇里进货。出镇时看见抱着周小贝在路上走的吴秋月，她拦下车告诉你周小贝生病了，急着去镇里的医院。你看着吴秋月哭得梨花带雨的，心里一软，立马让她上车送她去医院。到了医院以后医生让周小贝住院查看，交费时你看见吴秋月面露难色，难得到你表现。一共需要5000，你拿出了3500块的进货款和自己存了很多年的1500块私房钱，交完钱医生让你填资料，你一心都在吴秋月身上也没仔细听，照着护士指的地方写下了自己的名字。

周小贝被留在医院治疗，回村的路上奥拓的油不够了，两三百的油费太贵，几十块钱你又觉得拉不下面子，所以平时都是把车停得远远的拿个桶走到加油站告诉工作人员你是给摩托车加油的。你看了看旁边睡得正香的吴秋月，幸好她睡了要不然你还不知道怎么办。加好了50块的油以后继续开车回村，快到村口时你停下了车，路上你一直都在偷看吴秋月，想着你都给她垫付了5000的费用甚至连借条都没要，要是做点什么她应该也不会不同意吧。你壮了壮胆子扯过吴秋月想要和她来点什么，却没想到她反抗得这么激烈，最后竟然跑了。

你装了一肚子火，现在钱没了人也没到手，要是被文香知道你还不知道死得多惨，这时你想到了王话梅，这个老女人手上肯定有钱，于是你将吴大壮把周大宝推下山的事写成了一封匿名信给王话梅。要她准备5000元。在傍晚时放到周大宝的坟墓背后，吴大壮就是她的命所以她一定会答应。回到家后文香不在，而你坐着休息。文香回来之后问你去哪了，你哪里敢说实话就告诉她你进完货去小卖部整理新货了。谁知道文香突然发火和你吵了起来，她查了你的钱包后直接和你打了起来。你被她从屋内打到屋外，过去好歹只在家里打你，现在在外面都不给你留面子，根本不把你当个人。这两天本来就不顺隐忍多年的你突然不想忍下去了。快20年了你第一次还手打了个痛快，后来李丰收来了，他将文香护在了身后，你懒得和他们掰扯于是转头回家把门锁了。

晚上时你看人少，跑去了坟山，果然王话梅已经乖乖将钱放在了周大宝的坟后，这都是写5元十元的零钱还带着一股鱼腥味，不过只要是钱你都不在意。回来路过王话梅家田埂时看见了阿芝，这次你没有带糖，在田埂中你粗鲁的强暴了她。完事后心满意足的回到家里睡了过去，大约凌晨两三点，你睡的正香，突然，你的下体受到了重击，你惨叫着滚下了床，只看见门被关上，没看见是谁做的，随后你就晕了过去。

【拆迁第三天2010年9月7日 大风】

第二天你简直就是被活生生痛醒的，看了看时间大约十点，你爬起来穿上衣服准备去村医那里看看，正准备出门遇见了找上门的付大龙，文香没有在家，看你这个样子付大龙一脸同情，你觉得面子过不去，就告诉他文香这两天没有在家。付大龙听了以后到是没走，反而是问你想不想赌一赌，你不明白他什么意思就没有回答。他告诉你只要你同意拆迁，他有办法让你一个人得到所有的补偿款，你听到后激动非常，立刻扯着他问到底有什么办法，付大龙告诉你他这有个放弃拆迁补偿协议，只要你能弄到文香的手印，只要一盖章就能够让文香净身出户，你知道天下没有白吃的午餐，付大龙似乎知道你的顾虑，他告诉你只要你能够先拿5000块钱给他，最后得到拆迁款再给他三成他就能帮你。

你几乎没有犹豫就答应了，之后付大龙把协议给你，他告诉你明天之前一定要弄到文香的手印，你将从王话梅那里拿到的5000块交给付大龙，之后就是搞到手印的问题了。付大龙离开以后你去到村医那里，村医告诉你，你的下体受伤很严重，需要一大笔钱治疗，不然以后会失去男性功能。下午回到家你等了很久文香都没有回来，奇怪的是准备换衣服时却发现你衣柜里面的衣服全部不见了。你怀疑家里进了贼，但是现在当务之急是找到文香，找到晚上时突然听见老村长在喇叭里将李丰收骂了个狗血淋头，语气不善的让他赶紧滚回村委，没多久村南坟山起火了，火势很快就蔓延了，你想着文香一定会去查看她爹的坟，于是你拿上印泥直接就跑了到村南文山的坟前等着。等了一会儿文香果然来了，你随手拿了一块石头敲晕了她，拿着她的手按在了协议上，看着不远处的火势，只要能拿到拆迁你也难得管她会不会出事，你看了她一眼后转身就离开了这里。

【拆迁第四天 2010年9月8日阵雨转暴雨】

昨天火势很大，你也没有亲人被葬在这里，管他烧成什么样子，村里的人都被看住了不能离开，一个个的都被带到村委问话，下午时你因为房子离坟山近也被带了过，去，老村长将事情交给李丰收处理，而还没有被问话的只有你、文香、吴秋月、赵瓦、王话梅了。

你的任务：

1、隐瞒你杀死周大宝嫁祸吴大壮的事实。

2、隐瞒你强暴阿芝的事实。

3、找出是谁半夜袭击你

4、弄清楚其他几人在村子里面做过什么事情。

5、你和文香的感情，已经面临崩溃。如果文香要和你离婚，请一定要舔到底，否则你想独吞拆迁款的计划就没用了。（办理离婚都是找村支书，需要村支书成为证明人。适时你可以允诺村支书一些东西，让他不要签字）

6、调查清楚村南坟山发生大火的原因。

你对其他人的印象:

文香: 你的彪悍老婆，你人生悲剧的根源，结婚十九年同房四五次。

李丰收: 原名李狗蛋，最能装的村支书，不过听说人有怪癖，五年都不换被子。

王话梅: 村里的老寡妇，和文香不对付，十分大嘴巴只要她和别的老女人在一起，你路过时总是一脸鄙视。有个废物儿子。

吴秋月: 村里最美的女人，但你总是觉得周大宝不可能有本事娶到这样的美人。

赵瓦: 无赖，泼皮，每天盯着你的车不放过，可惜他弹一辈子棉花都买不起一个车轮。

其他

赵阿芝: 赵瓦的傻女儿，怕被和谐不细说了，非要说的话就一个字:白

吴大壮: 王话梅的儿子，村里的无赖之一，看起来一副没头没脑的样子

文山: 你的岳父，救了你父亲但是间接害了你的一生

付大龙: 拆迁经理，对你态度不错

周大宝: 如果不是他看见你和阿芝，也许你不会杀了他

【平时守店是真的无聊到抠脚】


