第二幕黑漆漆的夜

你们一个个的被叫去办公室里，在你前面的吴秋月、张来富、文香出来以后都是神色各异的，张来富刚出来就在角落用车钥匙上的水果刀削苹果吃，你看见李丰收找张来富拿了车钥匙，也不知道要干嘛。你原本想着不管付大龙说什么你都不会同意，谁知道付大龙直接用给你一张报纸，上面是关于当年帮派被抓的报道，当年的事情你一直不敢去打听，而你的通缉令也在其中。付大龙告诉你如果今天不把协议签了就会报警。你对他说如果你签了协议，自己的身份也会曝光。付大龙笑了笑告诉你：协议，是要签，不过我们有的是方法，正式拆迁时只要你把房子卖给我手底下的人，包你什么事都没有，只不过作为辛苦费，拆迁下来的钱得分他们60%。被人威胁的感觉并不好受，这些年平平安安的都过来了，现在居然来了这么一出。看你面色不快付大龙安慰你说：这个事只有我一个人知道，绝对不会泄露给第三人。说完他把协议拿到了你面前，无奈之下你只能咬牙签下了同意书。

出来以后你心情非常的糟糕，一支接一支的抽烟谁都不想搭理。王话梅是最后一个去谈的，其间吴秋月去了一趟厕所，回来时脸上的表情还留下些许震撼。你们谁都回不了家只能不情不愿的在这里吃着饭菜，席间付大龙很是开心，不停和李丰收喝着酒，没一会就喝高了。

<van-divider :style="{ color: '#ffb700', borderColor: '#ffb700', padding: '0 16px',}">小剧场pia戏</van-divider>
李丰收：来来来！吃好！喝好啊！就算拆迁了，村委也是想着大家的！组织上是绝对不会亏待大家的！

付大龙：李支书说..说.说得好！来大家鼓掌！我..我们拆迁团队，那可是非.非常常专业的，就是来.给大家谋福利的。秋月..你说..是不是呀？

吴秋月：付经理，您说的在理，您的工作我们一定全力支持。

文香：哟~咱们的吴大美人丈夫还没过头七吧？这是什么情况呀？找到下家了？

王话梅：总比有的人好啊，起码人家没了男人还有孩子呢。

文香：王大喇叭，你这嘴里不干不净说些什么呢？有孩子又怎么样，当妈的不争气，养出来的还不一样是个废物。

赵瓦：这酒桌上有你们女人什么事？这么多饭菜还堵不住你们的嘴吗？

吴秋月：那可不一定，这桌上有的女人可比男人还彪悍呢。

李丰收：都少说两句，一个村的关键是要团结。怎么能自己人打自己人呢。

张来富：李支书说得好啊，不过现在是一个村的，拆迁以后各走各路，谁也不耽搁谁。

赵瓦：那可不好说，这镇里都是水泥地，可没这村里的土跪着软和。

王话梅：人嘛，都是风水轮流转，有的人年轻的时候过得好，老了可能没依靠。

付大龙：够了！你们是不是都不…不不给我面子？

李丰收：付经理，别别，村里人没见识，您大人有大量别和他们计较。

赵瓦：呸！ 李丰收：来，经理，我扶您回房休息。

<van-divider :style="{color: '#ffb700',borderColor: '#ffb700',padding: '0 16px',}">pia戏结束</van-divider>
现在大雨封路，你们所有人都没办法离开村委会，于是李丰收把你们安排在了村委会的二楼，让你们对付一晚上，其余事明天再说。

付大龙居然知道了你加入帮派在逃的事，你绝不能让这个事情暴露出去。今天付大龙拿的那份报纸你一定要把它弄到手。夜晚时你来到付大龙的办公室，刚进办公室里面突然就停电了。该死的村子，每次下雨都这样，真是烦死了，等把报纸这个事情处理掉，一定要离开这个村子。

你只有凭着月光开始翻找着付大龙今天拿的那个文件袋，你看见报纸就在那个文件袋里面的，突然你听见门外传来了撞到东西的声音，而且脚步声似乎还是往你这个方向来的。你为了安全起见，你打开窗户躲到了窗子外面等待机会，到底是不是有人要进来。这时突然刮起了一阵风，把房间里面的文件吹得到处都是。

门打开了是张来富，他进来找什么东西？没一会，你看见了他拿出一个文件袋，还在那里得意的笑了笑，完了，难道这家伙也知道你的事情亦或者是付大龙准备给他说这个事情，特地让他来。你好不容易隐藏了这么多年。要想一个一石二鸟的办法，你突然想起来今天张来富把车钥匙借给了李丰收，车钥匙上有一把刀，不知道还给张来富了没有，你决定去李丰收的房间看看，如果找到了这把刀。到时候张来富一定会怀疑李丰收，张有富自己也洗脱不了嫌疑，如此一来这三个人一个开不了口说话，另外俩个人说的话也没人会相信，这是一个一石三鸟的计划。

你快步前往到了李丰收的房间里面，在房间里面打量起来，终于在桌上找到了那串车钥匙。你迅速离开李丰收的房间，突然一阵雷声。吓了你一个激灵，你决定回到房间里面平复一下心情，正当你打开门的时候，你发觉门上晃过一道光，你也没多想，回到了自己的房间，之后你听到过似乎是张来富在叫李丰收的名字，也不知道这个废物大半夜的找李丰收干什么？

休整了一会，刚打开门就听见了几声狗叫。你不想引起别人的注意，你悄悄的向一楼走去，来到一楼以后，打开了付大龙房间的门闻到了浓浓的酒味，看见付大龙这个家伙居然整个人躺在呕吐物上。看来醉得不轻阿，你顺手把付大龙房间的门锁上。这家伙居然睡觉还没睡在枕头上，你忍着恶心，左手压着付大龙的额头，使他的脖子漏出来，你拿的这一把小刀没有办法弄出更致命的伤口，你只有通过桶脖子让他死，于是你右手反手握着刀对着他的喉呢就是就桶了进去。刚刚桶完，就传来几声推门声，幸好刚才你把门给锁上了。你顺上将右手上的血迹，在付大龙的床上随便赠了蹭，之后你感觉门外的人走了，你迅速的离开了付大龙的房间。

