你来到李丰收的房间门口，尝试推了推门，没有反应。你顺口叫了叫他的名字还是没有反应，奇了怪了，大晚上的这个人能去哪里？没办法，你只有先回到房间，在房间里面想了起来，如果待会去找李丰收的时候被他抢走了这个记事本怎么办？于是你决定手抄一份，免得到时候被抢了没有证据，反正也只是数字，要不了多久就可以抄完。

过了一会，你把这个记事本抄完以后，听见了楼下传来了两声狗叫，你往窗外面看了看，也没发现什么，你又来到了李丰收的房间，他还是没在房间里面，唉，他这个事早晚都可以做，还是偷印章要紧，你径直来到了付大龙的房间，靠着门听了听里，面似乎没有声音了。你尝试敲了敲门看看他睡了没有，敲了几次都没反应，应该是睡着了。推门时发觉门还是一样锁死的。你决定从院子外翻窗户进入付大龙的房间。来到窗户外面，正好天气热大家习惯都不会关窗。你借着窗户的缝隙把窗户开，轻松的来到了房间里面，借着月光看见他在床上睡得死死的，于是你放心的在房间和付大龙的床边及枕头底下翻找印章。终于在他的上衣口袋里面找到了，你碰到章的同时，你还碰到了一个硬物，你退开仔细一开，付大龙的胸口上插着一把刀，你才发现你的双手和印章上全是血迹。

你被吓了一跳，你拿着印章直接从付大龙的房间冲了出去，回到了自己的房间，回房的途中你似乎撞倒了什么东西，回房以后你紧张万分，看着满手的血迹，你本想打开窗子用雨洗一下手，但没想到雨停了。于是你决定先把印章给盖了，到时候只要你死咬着不放，也没人知道你是在付大龙死后盖的章，还是生前盖的章了，这样房屋的拆迁款就全是你的了。

没过多久你就听见一声惨叫，杀人啦！杀人啦！村里的电线杆倒了，村民跑来村委会的办公室借电话，想看看能不能联系到外界，谁知道那个倒霉鬼一来就进了付大龙的屋子，发现死人以后敲锣打鼓的引来了村民，雨停了村委会不再是不可出入的，村民们将村委会围住，而你们几人自然就是最大的嫌疑犯。好事的村民请来了很久都不问世事的老村长，老村长要你们几人交出杀人的凶手，不然就按照村规将你们全部绑送警察局。

你的任务：

1、找到杀人凶手

2、根据自己的人设诬陷你最恨的人

3、在最后的时候，尽量说服大家拆迁

        </pre>
        </div>
        <div v-else content>暂未公开</div>
      </van-tab>

