        <van-tab title="整体案件复盘" class="content">
          <pre>
一、毒死王话梅家鱼的人：

李丰收，第二天深夜进入的到王话梅家中，将消毒用的两大袋生石灰倒入鱼塘中（李丰收手上有烧伤，生石灰和水产生反应会发热，同时鱼死亡当天王话梅曾经将李丰收及付大龙赶出家中）

二、杀害阿芝的凶手：

阿芝的死是多人造成的悲剧，看见张来富将周大宝推下山谷后被吓到，在山中躲了两天。第二日夜晚下山后路过吴秋月家中，吴秋月在给阿芝清理时阿芝说出：“周哥哥下去了，推下去了”吴秋月着急得到信息再次吓到阿芝，阿芝躲在王话梅家中田埂附近，路过的张来富粗鲁的强暴阿芝后离开。最后阿芝躲到了王话梅家中的地害里，地窖中有烂掉的白菜等，李丰收将饲料压在地害出口，王话梅听见家中有声音也因为心虚没有出门。最后赵瓦寻找时因为王话梅给出了错误的指示失去救出阿芝最后的机会。阿芝最终因为室息死在了地窖。（大家需要知道毒死王话梅家的鱼的人才能知道是谁杀害了阿芝。在王话梅的剧本里面都知道，地害就在鱼塘旁。而生石灰是被鱼饲料压着的，当李丰收把鱼饲料移过来以后才能拿得到生石灰，当李丰收把鱼饲料移过来的时候，把鱼饲料放在了地窖口。压住了地害的出口才导致了阿芝的死亡）

三、杀害周大宝的凶手：

张来富，吴大壮和周大宝产生争执之后将周大宝推下山谷，周大宝身手不错挂在了树枝上成功的爬了上去。刚上去就看见看热闹的张来富，这时赤裸的阿芝冲出来被周大宝看见，张来富因为害忙事情曝光再次将周大宝推了下去。（细节点：吴大壮回家后告诉王话梅自己把周大宝踢下去了，而阿芝告诉吴秋月的是周大宝被推了下去，再结合测量队证言，周大宝扒着树枝向上爬可知吴大壮不是杀害周大宝的凶手，阿芝最近常常上山，需要推理出是张来富诱奸阿芝，才可推断出张来富是杀死周大宝的凶手。怎么推出阿芝被侵犯过，吴秋月是知道阿芝被人侵犯过，平时村里的人要阿芝听话都是给的冰糖，赵瓦证据中出现阿芝的盒子里面有整理好的糖纸，村里面能够拿到这么多糖纸的只有张来富）

四、造成村东坟山起火的原因：

主要凶手为李丰收，为了制造村中风水不好的谣言，他偷取了村中拖拉机里面的柴油，到坟山后将柴油洒在了周围。之后李丰收因为老村长在广播中寻找他没有放火，但之后吴秋月去祭莫自己的丈夫，离开后没有烧尽的纸钱被大风吹到撒了柴油的芦苇中，最终造成大火。凶手被认定为李丰收，如果没有柴油火势并不会燃烧如此大。
            </pre
          >
        </van-tab>
        <van-tab title="凶案复盘逻辑" class="content">
          <pre>
凶案复盘逻辑：

1.今天进入付大龙房间的顺序

李丰收（灌酒，将付大龙面部朝下的放在床上）一一张来富（没有进去）一一王话梅
（偷手机）一一吴秋月（进去偷钱）一一文香（把付大龙翻过来，吾死）一一赵瓦
（插脖子）一一张来富（没有进去）一一王话梅（插前胸）一一张来富（找章）

2.凶案逻辑复盘：

在不给出任何降低难度线索的情况（如给了降低难度线索，主持人请加上降低难度线索的内容，自行参考下面复盘就行）：今天我们的死者付大龙，身中9刀，前胸8刀、脖颈处1刀。首先我们要找到关于这两把刀的持有者，我们先来看前胸的8刀。在以往的剧本杀游戏当中，如果行凶者是在当时的环境里面拿的凶器，那么我们可以找到相应地点的证据，但是今天并没有出现过任何会有出现刀的地方。也就是说今天这把刀是行凶者随身带在身上的，那么今天在场的6个人里面有谁是平日拿着刀在大马路上走，都不会引起别人怀疑的呢？没错就是我们的王话梅，她自己本身就是杀鱼的。加上今天王话梅完全是被人绑着过来的，在我们李丰收和文香的视角里面会知道，今天王话梅是衣服都没换就直接过来了。也就是说今天她是穿着平时杀鱼的衣服过来的，那她的刀放在那里的呢，就放在皮围裙的前口袋里面，而今天我们王话梅的杀人动机，就是自己的儿子被付大龙叫人打死的。

那脖颈的刀我们接着来看，脖颈处被尖锐物捕伤的，那今天现场还有哪里存在的有尖锐物，就只剩我们李丰收房间里面的车钥匙。分贝村里只有一个人有车，那想当然的这个车钥匙当然就是我们张来富的，那张来富是不是凶手，答案：不是。那问题就来了，有谁会知道张来富的车钥匙上有把水果刀。想想平时有哪些人能观察到张来富的车钥匙，一个是文香，一个是今天借车钥匙的李丰收，还有一个那就是长期和我们文香偷情的赵瓦。而今天李丰收找张来富借车钥匙的时候赵瓦就在旁边看着。再换一个思路来说，车钥匙可以说是张来富身份识别的东西，他如果要杀人当然不会用自己车，钥匙上面的刀，而李丰收作为村子里面唯一读过书的人，他也不会把杀人凶器就放在自己的房间里面。结合线索，赵瓦的指甲缝里面会有呕吐物。那如果要杀一个人的脖颈处，是不是需要用手稳住死者的头部，而今天死者满脸都是呕吐物，所以，今天赵瓦是脖颈处动刀的人。

现在两把刀都找出来了，那是不是他们杀的人呢，答案：不是。

结合两位嫌疑人的证据，以及他们自己的视角，赵瓦和王话梅两人身上都没有喷溅型血迹，现场也没有喷溅型的血迹，也就是说，今天我们两位动刀的人，杀的都是死人。

那真正的凶手是谁？答案：文香。

尸体双臂上有长约6公分的紫黑色游青是怎么造成的，今天大家都知道两个事情，第死者睡相很奇怪没有睡在枕头上。第二，死者周围基本全都是呕吐物。既然两把刀都没有致死，那就要考虑这个奇怪的枕头了，枕头左侧有呕吐物，右侧没有，这条信息主要是想告知李丰收今天并不是杀人凶手，李丰收今天自己会知道，他做了两手准备，他让付大龙趴在床上，，一、是用工业酒精毒死他，二、是用付大龙自己的呕，吐物闷死他。左侧有呕吐物也就是说，付大龙自己趴着吐不出来，自己转向了左边吐了，（在王话梅的剧本里面也会提到“你开门离开的时候似乎听见身后传来声响”），然后被真正的杀人凶手文香给翻了过来，所以左耳里面才会也有呕吐物。那死者为什么会没有睡在枕头上，是因为今天文香上了床，用双腿跪在付大龙的上臂处，接着用枕头吾死了他，这就是死者手臂上两条约6公分粗细的紫黑色淤青的由来，而文香为了掩盖自己裤子沾有呕吐物的事实，她去厨房把全身都打湿了，来掩盖自己只有裤子是湿的事情，所以为什么只有文香身上是湿的，头发不是湿的原因。还有就是为什么明明是3天前才带上的戒指，会有一圈压痕，是因为括死死者时，手部用力的原因造成的。

常见问题回答：

Q：张来富的母亲给文山说了什么？

A：孩子他爹现在重病在床，我也没办法，这个孩子就算卖给你了。

二：Q：吴大壮为什么偷内衣？

A：从小因为王话梅的娇生惯养，养成了不好的习惯的，总觉得女性的内衣奇特，想偷来自己观察，久而久之，就养成了偷内衣的习惯。自从见到了吴秋月以后，非常喜欢吴秋月，没办法得到她，就去偷了她的内衣。

三：Q：王话梅家的腊肠是谁偷的

A：是赵阿芝从山上下来，被吴秋月吓到以后，躲在王话梅家，肚子饿了扯来吃
            </pre
          >
