<script>
import { Dialog } from 'vant';
import { ImagePreview } from 'vant';
export default {
  data () {
    return {
      role: localStorage.getItem('game03'),
      active: 0,
      activeEnd: 0,
      isShowClue: false,
      isShowEnd: false,
      activeCLue: 0,
      activeClue1: 11,
      activeClue2: 11,
      level: 1,
      note: localStorage.getItem('game03Note') || "",
      levelText: "第一轮搜证",
      items1: [],
      items2: [],

    }
  },
  methods: {
    levelUp () {
      if (this.level <= 3) {
        Dialog.confirm({
          title: '是否解锁',
          message: '请根据主持人安排解锁',
        })
          .then(() => {
            if (this.level == 1) {
              this.level++,
                this.activeClue1 = 0,
                this.levelText = "解锁第二幕",
                this.items1.push({ text: '文香' }, { text: '李丰收' }, { text: '王话梅' }, { text: '吴秋月' }, { text: '张来富' }, { text: '赵瓦' }, { text: '村民传言' }, { text: '公共证据' })
            } else if (this.level == 2) {
              this.level++,
                this.levelText = "第二轮搜证"
            } else if (this.level == 3) {
              this.activeClue2 = 0,
                this.items2.push({ text: '文香' }, { text: '李丰收' }, { text: '王话梅' }, { text: '吴秋月' }, { text: '张来富' }, { text: '赵瓦' }, { text: '死者信息' }, { text: '死者房间' }, { text: '公共证据' }, { text: '搜身证据' })
              this.level++,
                this.levelText = "查看真相"
            }
          })
          .catch(() => {
            // on cancel
          });
      } else if (this.level >= 4) {
        Dialog.confirm({
          title: '确认是否显示结局',
          message: '请勿提前开启结局',
        })
          .then(() => {
            this.isShowEnd = true
          })
          .catch(() => {
            // on cancel
          });
        // console.log(23);
      }
    },
    ImagePre (url) {
      ImagePreview([
        url
      ]);
    },
    saveNote () {
      localStorage.setItem('game03Note', this.note)
    }
  }
}
</script>

